import React from "react";
import Form from "../Components/Form";
import Joi from "joi-browser";
import { authHandle } from "../App";

export default class Login extends Form {
  state = {
    data: { email: "", password: "" },
    errors: {},
  };

  schema = {
    email: Joi.string().required().email().label(" "),
    password: Joi.string().required().label(" "),
  };

  doSubmit = async () => {
    const errors = await authHandle("http://localhost:3001/login", this.state);
    this.setState({ errors });
  };

  render() {
    return (
      <>
        <div className="main p-0 m-0 position-absolute w-100">
          <div className="w-50 mx-auto my-5 pt-5">
            <div className="row contain bg-light px-5 py-4 w-100 d-flex align-items-center justify-content-center">
              <form className="px-5" onSubmit={this.onSubmit}>
                <h3 className="text-center m-0 mb-4"></h3>
                {this.renderInput("email", " ", "text", true)}
                {this.renderInput("password", " ", "password")}
                {this.renderButton("")}
              </form>
            </div>
          </div>
        </div>
      </>
    );
  }
}
